import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';

import FieldGenerator from 'components/FormsV3/FieldGenerator';
import Button from 'components/FormsV3/Button/Button';
import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import CheckoutComplete from 'components/Templates/CheckoutPopup/CheckoutComplete';

import { sendEmailForm } from 'actions/insuranceActions';

import { validateFormValue, validateForm } from 'lib/validatorsV2';
import './FlowEnquiry.scss';

const forms = [{
  size: 12,
  title: 'Email',
  slug: 'email',
  placeholderString: 'Enter company email address',
  type: 'email',
  readOnly: true,
  is_required: true,
}, {
  size: 6,
  title: 'Full name',
  slug: 'full_name',
  placeholder: 'E.g John Doe',
  type: 'text',
  is_required: true,
}, {
  size: 6,
  title: 'Mobile no',
  slug: 'mobile_number',
  placeholder: 'E.g 8888 8888',
  type: 'number',
  is_required: true,
}];

const disclaimerHeader = 'I agree that none of my employees are:';
const disclaimerItems = [
  'Based outside of Singapore.',
  'Engaged in hazardous occupation. (E.g. welder, diver, sandblaster, offshore workers etc)',
  'Currently hospitalised or requires frequent admission (e.g. hospital admission more than 2 times per year) to the hospital.',
  'Suffer from any serious condition such as cancer, organ failure, heart disease, stroke, liver disorder, arthritis or any other disorder that cause progressive irreversible functional or physical disability.',
];

class FlowEnquiry extends Component {
  constructor(props) {
    super(props);

    const { email } = this.props;

    this.state = {
      values: { email },
      errors: {},
      formState: 0,
      errorMsg: '',
    };

    this.onChangeValue = this.onChangeValue.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.hideModal = this.hideModal.bind(this);
  }

  // Update form values on change
  async onChangeValue(type, name, value) {
    const { values, errors } = this.state;

    const error = errors && errors[name];
    let newError = {};

    if (error) {
      newError = { [name]: await validateFormValue(name, { type, value }, values, forms) };
    }

    this.setState({
      values: {
        ...values,
        [name]: { type, value },
      },
      errors: { ...errors, ...newError },
    });
  }

  async handleSubmit() {
    const { values } = this.state;
    const { handleAPI, errors } = this.props;

    try {
      const newErrors = await validateForm(values, null, forms);
      if (!_.isEmpty(newErrors)) {
        this.setState({
          errors: newErrors,
        });

        return;
      }

      const requestData = {
        form_category: 'Business (Purchase Flow)',
        input_data: _.map(forms, f => ({
          name: f.title,
          value: values[f.slug].value,
          type: 'text',
        })),
      };

      const result = await handleAPI(() => sendEmailForm(requestData));

      if (result) {
        if (this.checkoutComplete) {
          setTimeout(() => {
            this.checkoutComplete.openCheckoutComplete();
          }, 300);
        }
      } else {
        this.setState({
          errors: {
            ...errors,
            errorMsg: 'Request failed! Please try again.',
          },
        });
      }
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  hideModal() {
    const { hideEnquiryModal, email } = this.props;

    this.setState({
      values: { email },
      errors: {},
      formState: 0,
      errorMsg: '',
    });

    if (hideEnquiryModal) {
      hideEnquiryModal();
    }
  }

  renderDisclaimer() {
    return (
      <Fragment>
        <p className="desc-text">{disclaimerHeader}</p>
        <ol>
          {
            _.map(disclaimerItems, (item, index) => (
              <li key={`discliamer-${index}`} className="desc-text">{item}</li>
            ))
          }
        </ol>
      </Fragment>
    );
  }

  renderForm() {
    const { values, errors, errorMsg } = this.state;

    return (
      <Row noGutters>
        <Col xs={12}>
          <p className="desc-text">Based on your declarations, your employees conditions may require a customized plan. Please leave down your contact details and we will contact you within 2 business days.</p>
        </Col>
        {
          _.map(forms, (form, index) => (
            <FieldGenerator
              key={`business-form-${index}`}
              values={values}
              errors={errors}
              fieldData={form}
              onChangeValue={this.onChangeValue}
              extraInfo={form.extra_info && <p>{form.extra_info}</p>}
            />
          ))
        }
        <Col xs={12}>
          <p className="error-text">{errorMsg}</p>
        </Col>
      </Row>
    );
  }

  renderDisclaimerButton() {
    const { onNext } = this.props;

    return (
      <Row className="w-100" noGutters>
        <Col className="button-container" xs={12} sm={6}>
          <Button
            className="button-left cancel-btn"
            variant="medium-outline"
            height={48}
            width={162}
            onClick={() => this.setState({ formState: 1 })}
          >
            <p>No, I do not agree</p>
          </Button>
        </Col>
        <Col className="button-container" xs={12} sm={6}>
          <Button
            variant="medium-flat-green"
            height={48}
            width={162}
            onClick={onNext}
          >
            <p>Yes, I agree</p>
          </Button>
        </Col>
      </Row>
    );
  }

  renderFormButton() {
    return (
      <Row className="w-100" noGutters>
        <Col className="button-container" xs={12}>
          <Button
            variant="medium-flat-green"
            height={48}
            width={114}
            onClick={this.handleSubmit}
          >
            <p>Submit</p>
          </Button>
        </Col>
      </Row>
    );
  }

  render() {
    const { formState } = this.state;
    const { showModal, history } = this.props;

    try {
      return (
        <div className="business-flow-enquiry-container">
          <ModalPopup
            showModal={showModal}
            closeModal={this.hideModal}
            className="business-flow-enquiry-modal"
            backdropClassName="blue-modal-backdrop"
            header={<p>{formState === 0 ? 'Policy Restrictions' : 'Thank you for your interest in EZCare PAL!'}</p>}
            body={formState === 0 ? this.renderDisclaimer() : this.renderForm()}
            footer={formState === 0 ? this.renderDisclaimerButton() : this.renderFormButton()}
          />
          <CheckoutComplete
            className="blue-popup-backdrop"
            isQuoteOnly
            upperText="Submission Successful!"
            midText="Thank You for Choosing PolicyPal! We will be contacting you within 2 business days."
            buttonText="Confirm"
            wrappedComponentRef={node => this.checkoutComplete = node}
            callback={() => history.push('/')}
          />
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

FlowEnquiry.defaultProps = {
};

FlowEnquiry.propTypes = {
  showModal: PropTypes.bool.isRequired,
  hideEnquiryModal: PropTypes.func.isRequired,
  handleAPI: PropTypes.func.isRequired,
  onNext: PropTypes.func.isRequired,
  email: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};

export default withRouter(FlowEnquiry);
